.ProfileContainer {
    display: flex;
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0%;
    left: 0%;
    max-width: 100%;
    background-color: white;
  }
  
  .ProfileContainer > div {
    display: flex;
    height: 100vh;
    width: 100vw;
    max-width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .ProfileContainer > div:first-child > h1 {
    color: rgba(0, 0, 0, 0.555);
    /* font: 500 2.2vmax "Roboto"; */
    transform: translateX(-10vmax) translateY(-2vmax);
    font-family: 'Fredoka One';
    font-size: 2.5vmax;
    margin-top: -26vmax;
    /* margin: 1vmax 20vmax; */
    margin-top: 16vmax;
    margin-left: 19vmax;
}
  
  
  .ProfileContainer > div:first-child > img {
    width: 20vmax;
    border-radius: 100%;
    transition: all 0.5s;
  }
  
  .ProfileContainer > div:first-child > img:hover {
    transform: scale(1.05);
  }
  
  .ProfileContainer > div:first-child > a {
    border: none;
    background-color: rgb(59 73 113);
    /* font: 400 1vmax Cursive; */
    color: white;
    text-decoration: none;
    padding: 0.5vmax;
    width: 17%;
    margin: 7vmax -1vmax;
    text-align: center;
    transition: all 0.5s;
    /* margin-left: -10vmax; */
    /* margin-left: 93px; */
    padding: 2vmax;
    border-radius: 10vmax;
    margin-bottom: 15vmax;
    margin-top: 0vmax;
    font-family: 'Permanent Marker';
  }
  
  .ProfileContainer > div:first-child > a:hover {
    background-color: rgb(204, 78, 56);
  }
  
  .ProfileContainer > div:last-child {
    justify-content: space-evenly;
    align-items: flex-start;
    padding: 5vmax;
    box-sizing: border-box;
  }
  
  .ProfileContainer > div:last-child > div > h4 {
    color: rgb(21, 24, 21);
    /* font: 700 1.2vmax "Roboto"; */
    font-family: 'Permanent Marker';
  }
  
  .ProfileContainer > div:last-child > div > p {
    color: rgba(54, 13, 116, 0.418);
    font: 400 1.5vmax cursive;
    margin: 0.2vmax;
    font-weight: bolder;
    font-size: 1.7vmax;
  }
  
  .ProfileContainer > div:last-child > div:last-child {
    display: flex;
    flex-direction: column;
    width: 60%;
  }
  
  .ProfileContainer > div:last-child > div:last-child > a {
    border: none;
    background-color: rgb(68, 68, 68);
    /* font: 400 1vmax "Roboto"; */
    color: white;
    text-decoration: none;
    padding: 0.5vmax;
    text-align: center;
    transition: all 0.5s;
    margin: 1vmax 0;
    border-radius: 2vmax;
    font-family: 'Fredoka One';
  }
  
  .ProfileContainer > div:last-child > div:last-child > a:hover {
    background-color: rgb(31, 31, 31);
  }
  
  @media screen and (max-width: 600px) {
    .ProfileContainer {
      flex-direction: column;
    }
  
    .ProfileContainer > div:first-child > h1 {
      font: 500 3.2vmax "Roboto";
      transform: translateY(-2vmax);
    }
  
    .ProfileContainer > div:first-child > a {
      font: 400 1.7vmax "Roboto";
      padding: 1vmax;
    }
  
    .ProfileContainer > div:last-child {
      text-align: center;
      align-items: center;
    }
  
    .ProfileContainer > div:last-child > div > h4 {
      font: 400 2.5vmax "Roboto";
    }
  
    .ProfileContainer > div:last-child > div > p {
      font: 400 2vmax cursive;
      margin: 0.5vmax 0;
    }
  
    .ProfileContainer > div:last-child > div:last-child > a {
      font: 400 1.8vmax "Roboto";
      padding: 1vmax;
      margin: 2vmax 0;
    }
  }